import { ChangeEvent, FormEvent, useEffect, useState } from 'react';
import jwt from "jsonwebtoken";
import axios from "axios";
import './App.css';
import LOGO from "./assets/images/logo.png";
import LOADING from "./assets/images/ajax-loader.gif";

//https://j2txrxxfga.execute-api.us-east-1.amazonaws.com/dev/forgot-password
const PASS = "@jobfishing";
const URL = process.env.NODE_ENV === "development" ? 
  "http://localhost:3000/dev/reset-password" : 
  "https://j2txrxxfga.execute-api.us-east-1.amazonaws.com/dev/reset-password";

function getUrlVars() {
  var vars:any = {};
  var parts:any = window.location.href;
  parts.replace(/[?&]+([^=&]+)=([^&]*)/gi,
  (m:any,key:any,value:any) => {
    vars[key] = value;
  });
  return vars;
}

type TProps = {
  status: boolean,
  loaded: boolean,
  password?: string,
  confirmPassword?: string,
  token?:string,
  loading?: boolean
}

const INITIAL_VALUE:TProps = {password:"", confirmPassword:"", status: false, loaded: false, loading: false};

function App() {
  const [state, setState] = useState<TProps>(INITIAL_VALUE);

  useEffect(() => {
    async function load(){
      const _token = getUrlVars()["token"];
    
      if(_token){
        try {
          jwt.verify(_token, PASS);
          setState((previous:TProps) => ({...previous, status: true, loaded: true, token: _token}));
        } catch (error) {
          setState((previous:TProps) => ({...previous, status: false, loaded: true}));
        }
      } else{
        setState((previous:TProps) => ({...previous, status: false, loaded: true}));
      }
    }
    load();
  }, []);

  function submitHandler(e:FormEvent){
    e.preventDefault();

    if(state.password && state.confirmPassword){
      if(state.password !== state.confirmPassword){
        alert("Confirm password must be the same as password");
        return false;
      }

      if(state.password.length < 5){
        alert("Password must have at least 5 characters!");
        return false;
      }
      savePassword();
    } else {
      alert("Fill in all fields!");
    }

  }

  function savePassword(){
    setState((previous:TProps) => ({...previous, loading: true}));
    try {
      const headers:any = {
        'LANGUAGE': 'EN',
        'TOKEN_API': 'am9iZmlzaGluZw=='
      }
      const data = {
        token: state.token,
        password: state.password
      };
      axios.post(URL, data, {headers})
      .then(function (response) {
        alert("Password reseted successfully!");
        hideLoading();
      })
      .catch(function (error) {
        alert("Error (1001)");
        hideLoading();
      });
    } catch (error) {
      alert("Error (1002)");
      hideLoading();
    }
  }

  function hideLoading(){
    setState((previous:TProps) => ({...previous, loading: false}));
  }

  function inputHandler(e:ChangeEvent<HTMLInputElement>){
    const {name, value} = e.target;
    setState((previous:TProps) => ({...previous, [name]: value}));
  }

  if(state.loaded && !state.status){
    return (
      <div className="App">
        <div className='box-brand'>
          <img width={200} src={LOGO} alt="" />
        </div>
        
        <form action="">
          <h6>Invalid token</h6>
        </form>
    </div>
    );
  }

  return (
    <div className="App">
        <div className='box-brand'>
          <img width={200} src={LOGO} alt="" />
        </div>
        
        <form onSubmit={submitHandler}>
          <h6>Reset your password</h6>
          <input 
          value={state.password} 
          onChange={inputHandler} 
          name='password' 
          type="password" 
          placeholder='Your new password' />
          <input 
          onChange={inputHandler} 
          value={state.confirmPassword} 
          name="confirmPassword"  
          type="password" 
          placeholder='Confirm your password' />
          <input type="submit" value="Save" className='button-submit' />
        </form>
        {state.loading && <div className='modal-loading'>
          <span>
            <img src={LOADING} alt="Loading" />
          </span>
        </div>}
    </div>
  );
}

export default App;
